@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,600,700,800,900&display=swap);
.sidebar-container {
  order: -1;
  width: 25rem;
  height: 100%;
  background-color: #173f5f;
  font-size: 1.6rem;
  color: #fff;
}

.sidebar-grid {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-top: 7rem;
}

.sidebar-item {
  list-style: none;
  letter-spacing: 1.3px;
  font-size: 1.8rem;
  width: 100%;
  height: 7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  font-weight: 300;
  text-decoration: none;
  text-transform: capitalize;
  color: #e3e3e3;
  font-weight: 600;
}

.sidebar-item.active,
.sidebar-item:hover {
  box-shadow: inset 5px 0 0 #91eae1;
  color: #91eae1;
}

.main-container {
	height: 100%;
	width: 100%;
	display: grid;
	grid-template-columns: repeat(8, 1fr);
	grid-template-rows: repeat(8, 1fr);
	font-size: 2rem;
	background-color: #fff;
	overflow: hidden;
	position: relative;
}

.background {
	background-color: #20639b;
	grid-row: 1 / 5;
	grid-column: 1 / -1;
}

.fas {
	font-size: 3rem;
	color: #3caea3;
}

.arrow {
	align-self: center;
	-webkit-transition: -webkit-transform 0.3s ease;
	transition: -webkit-transform 0.3s ease;
	transition: transform 0.3s ease;
	transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.arrow:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

.coming-soon {
	grid-row: 4 / span 2;
	grid-column: 4 / span 2;
}

.badge-container {
	align-self: center;
	grid-column: 1 / span 2;
	justify-self: baseline;
	margin-left: 2rem;
	font-size: 1.8rem;
}

.badge-count {
	margin-left: 1rem;
	background-color: #3caea3;
	color: #ffffff;
	padding: 1rem;
	border-radius: 50%;
	font-size: 1.5rem;
	display: inline-block;
	width: 2rem;
	height: 2rem;
	vertical-align: middle;
}

button {
	grid-row: 8 / 9;
	grid-column: 7 / 8;
	width: 10rem;
	border-radius: 10px;
	height: 3.5rem;
	align-self: flex-end;
	background-color: #3caea3;
	color: #fff;
	cursor: pointer;
	box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
}

button:hover {
	box-shadow: none;
	-webkit-transform: translate(2px, 2px);
	        transform: translate(2px, 2px);
}

button:focus {
	outline: none;
}

.delete {
	background-color: red;
	grid-row: 1 / 2;
	grid-column: 8 / 9;
	align-self: center;
}

.dashboard-form-container {
  grid-row: 2 / span 6;
  grid-column: 2 / span 6;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(8, 1fr);
  background-color: #ffffff;
  text-align: center;
  padding: 2rem 0;
  border-radius: 5px;
  box-shadow: 2px 4px 20px rgba(0, 0, 0, 0.15);
  position: relative;
}

.dashboard-form-group {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.dashboard-form-group input {
  width: 45%;
  border: none;
  border-bottom: 1px solid #3caea3;
  -webkit-transition: -webkit-transform 0.2s ease;
  transition: -webkit-transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}

.dashboard-form-group input:not([type='file']) {
  padding-bottom: 1rem;
  font-size: 1.4rem;
}

.dashboard-form-group input:focus {
  outline: none;
  -webkit-transform: translate(2px, 2px);
          transform: translate(2px, 2px);
}

.dashboard-form-group.one {
  grid-row: 1 / 2;
  grid-column: 2 / span 6;
}

.dashboard-form-group.two {
  grid-row: 3 / 4;
  grid-column: 2 / span 6;
}

.file-input-container {
  width: 45%;
  border-bottom: 1px solid #3caea3;
  display: flex;
  justify-content: flex-start;
}

.file-input-container label {
  font-size: 1.4rem;
  background-color: #3caea3;
  color: #fff;
  padding: 1rem 1.5rem;
  cursor: pointer;
  z-index: 10;
}

label.disabled {
  background-color: #ccc;
  cursor: auto;
  cursor: initial;
}

.file-input-container input {
  width: 100%;
  opacity: 0;
  position: absolute;
}

.file-input-container span {
  align-self: flex-end;
  font-size: 1.2rem;
  flex: 1 1;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

textarea {
  grid-row: 5 / 8;
  grid-column: 2 / span 6;
  border: 1px solid #3caea3;
  padding: 1rem;
  font-size: 1.5rem;
}

textarea:focus {
  outline: none;
}

.update-image {
  box-shadow: 2px 4px 2rem rgba(0, 0, 0, 0.15);
  height: 40rem;
  width: 50rem;
  z-index: 30;
  position: absolute;
  -webkit-transform: scale(0);
          transform: scale(0);
  opacity: 0;
  -webkit-transition: all 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: all 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.modal-shroud.show .update-image {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
}

.update-image img {
  height: 100%;
  width: 100%;
}

.fa-eye {
  color: #173f5f;
  font-size: 3rem;
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.fa-eye:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.fa-eye::before {
  cursor: pointer;
}

.loader {
  grid-row: 2 / span 6;
  grid-column: 2 / span 6;
  box-shadow: 2px 4px 20px rgba(0, 0, 0, 0.15);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.login-container .loader {
  box-shadow: none;
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.loader i {
  font-size: 8rem;
  -webkit-animation: rotate 1s linear infinite;
          animation: rotate 1s linear infinite;
}

.the-dashboard {
  -webkit-transform: rotate(-10deg);
          transform: rotate(-10deg);
}

path {
  stroke: #fff;
  fill: #fff;
  stroke-dasharray: 9000;
  stroke-miterlimit: 10;
  stroke-width: 1;
  -webkit-transition: opacity 500ms ease;
  transition: opacity 500ms ease;
  -webkit-animation: animate 3s cubic-bezier(0, 0.23, 1, 0.1) forwards;
          animation: animate 3s cubic-bezier(0, 0.23, 1, 0.1) forwards;
}

@-webkit-keyframes animate {
  0% {
    opacity: 0;
    stroke-dashoffset: 9000;
  }

  30% {
    opacity: 1;
    stroke-dashoffset: 9000;
  }

  90% {
    opacity: 1;
    fill: rgba(255, 255, 255, 0);
  }

  100% {
    fill: rgba(255, 255, 255, 1);
    opacity: 1;
    stroke-dashoffset: 0;
  }
}

@keyframes animate {
  0% {
    opacity: 0;
    stroke-dashoffset: 9000;
  }

  30% {
    opacity: 1;
    stroke-dashoffset: 9000;
  }

  90% {
    opacity: 1;
    fill: rgba(255, 255, 255, 0);
  }

  100% {
    fill: rgba(255, 255, 255, 1);
    opacity: 1;
    stroke-dashoffset: 0;
  }
}

.login-container {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.login-form {
  height: 40rem;
  width: 50rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  opacity: 0;
  -webkit-animation: fade-in 2s ease 2s forwards;
          animation: fade-in 2s ease 2s forwards;
}

@-webkit-keyframes fade-in {
  to {
    opacity: 1;
  }
}

@keyframes fade-in {
  to {
    opacity: 1;
  }
}

.login-container .form-control-group {
  width: 30rem;

  margin-bottom: 2rem;
  font-size: 1.4rem;
}

.login-container input {
  width: 100%;
  font-size: 1.8rem;
  border: none;
  border-bottom: 1px solid #3caea3;
  -webkit-transition: -webkit-transform 0.2s ease;
  transition: -webkit-transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
  text-transform: inherit;
  padding: 1.2rem;
  background: transparent;
  color: #f2f2f2;
}

.login-container input::-webkit-input-placeholder {
  color: #f2f2f2;
  font-size: 1.8rem;
}

.login-container input::-moz-placeholder {
  color: #f2f2f2;
  font-size: 1.8rem;
}

.login-container input:-ms-input-placeholder {
  color: #f2f2f2;
  font-size: 1.8rem;
}

.login-container input::-ms-input-placeholder {
  color: #f2f2f2;
  font-size: 1.8rem;
}

.login-container input::placeholder {
  color: #f2f2f2;
  font-size: 1.8rem;
}

.login-container input:focus {
  outline: none;
  -webkit-transform: translate(2px, 2px);
          transform: translate(2px, 2px);
}

.modal-shroud {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  grid-row: 2 / span 6;
  grid-column: 2 / span 6;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.25);
  z-index: 10;
  display: flex;
  opacity: 0;
  -webkit-transform: scale(0);
          transform: scale(0);
}

.modal-shroud.show {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
}

.modal-shroud.show .modal-container {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
}

.modal-container {
  background-color: #fff;
  height: 30rem;
  width: 40rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  -webkit-transform-origin: center;
          transform-origin: center;
}

.modal-button-container {
  display: flex;
  justify-content: space-around;
  width: 70%;
}

.modal-close-btn {
  position: absolute;
  top: 2rem;
  right: 5rem;
  font-size: 5rem;
  cursor: pointer;
}

.modal-close-btn:hover {
  color: #fff;
}

.single-project-container {
	height: 15rem;
	width: 15rem;
	border: 2px solid #e3e3e3;
	padding: 0.2rem;
	cursor: pointer;
	-webkit-transition: -webkit-transform 0.3s ease;
	transition: -webkit-transform 0.3s ease;
	transition: transform 0.3s ease;
	transition: transform 0.3s ease, -webkit-transform 0.3s ease;
	position: relative;
	overflow: hidden;
}

.single-project-container:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

.single-project-container:hover .single-project-title {
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

.single-project-container:hover .single-project-image {
	-webkit-filter: blur(2px) opacity(0.5);
	        filter: blur(2px) opacity(0.5);
}

.single-project-image {
	height: 100%;
	width: 100%;
	-webkit-transition: all 0.3s ease;
	transition: all 0.3s ease;
}

.single-project-title {
	font-size: 1.8rem;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, 600%);
	        transform: translate(-50%, 600%);
	-webkit-transition: -webkit-transform 0.3s ease;
	transition: -webkit-transform 0.3s ease;
	transition: transform 0.3s ease;
	transition: transform 0.3s ease, -webkit-transform 0.3s ease;
	font-weight: bold;
}

.projects-container {
  grid-column: 2 / span 6;
  grid-row: 2 / span 6;
  box-shadow: 2px 4px 20px rgba(0, 0, 0, 0.15);
  display: grid;
  grid-template-columns: repeat(4, minmax(10rem, 1fr));
  grid-template-rows: repeat(3, minmax(10rem, 1fr));
  justify-items: center;
}

.projects-container a {
  color: #000;
  text-decoration: none;
}

.no-projects {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-column: 2 / span 2;
  grid-row: 2 / span 1;
}

.tags-container {
  box-shadow: 2px 2px 1rem rgba(0, 0, 0, 0.15);
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  width: 30rem;
  background-color: #fff;
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
  -webkit-transform: translateX(98%);
          transform: translateX(98%);
  z-index: 10;
}

.tags-toggle-btn {
  background-color: #173f5f;
  border-radius: 1rem;
  position: absolute;
  top: 50%;
  left: 0;
  height: 3rem;
  width: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-transform: translate(-2rem, -50%);
          transform: translate(-2rem, -50%);
  cursor: pointer;
}

.tags-form-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.tags-container.active {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.tags-form {
  flex: 1 1;
  justify-self: flex-end;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.tags-text-input {
  border: none;
  border-bottom: 1px solid #3caea3;
  outline: none;
  -webkit-transition: -webkit-transform 0.2s ease;
  transition: -webkit-transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
  text-transform: inherit;
  font-size: 1.4rem;
  padding-bottom: 1rem;
}

.tags-text-input:focus {
  outline: none;
  -webkit-transform: translate(2px, 2px);
          transform: translate(2px, 2px);
}

.tags-text-btn {
  opacity: 0;
  position: absolute;
}

.tags-toggle-btn i {
  color: #fff;
  font-size: 1.6rem;
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.tags-toggle-btn i.rotate {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.tags {
  align-content: flex-start;
  align-items: flex-start;
  display: flex;
  flex-wrap: wrap;
  flex: 5 1;
  justify-content: center;
  padding: 1rem;
  width: 99%;
}

.tag {
  background-color: #ed553b;
  color: #fff;
  border-radius: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  margin: 1rem 0.5rem;
  padding: 1rem;
  width: 5rem;
  position: relative;
}

.tag-close {
  position: absolute;
  top: -3px;
  right: 5px;
